@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.content {
  min-height: 100vh;
  background: bottom left/76vw no-repeat url(pattern-curve.svg), white;
}
@media (orientation: landscape) {
  .content {
    background-size: 42.36vw;
  }
}

.testimonials {
  max-width: 64rem;
  margin: 0 auto;
  padding: 3.6vh 6.4vw;
}
@media (orientation: landscape) {
  .testimonials {
    display: flex;
    flex-flow: row-reverse;
    align-items: center;
    max-width: none;
    padding: 6.25vh 5.07vw 0 164px;
  }
}

.slide {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 4.5vh 9.6vw 0;
  background: center/contain no-repeat url(pattern-bg.svg);
}
@media (orientation: landscape) {
  .slide {
    flex: 0 0 48.4vw;
    align-items: flex-start;
    padding: 8vh 6.39vw 3.25vh 4.44vw;
  }
}
.slide > .photo > img {
  width: 100%;
  border-radius: 0.4rem;
  box-shadow: 0 1.6rem 4rem rgba(32, 32, 70, 0.2);
}

.controls {
  display: flex;
  margin-top: calc(-1rem - 2.7vmin);
}
@media (orientation: landscape) {
  .controls {
    margin-top: -2.8rem;
    margin-left: 6.2rem;
  }
}
.controls > button {
  z-index: 1;
  display: block;
  width: calc(2rem + 5.33vmin);
  height: calc(2rem + 5.33vmin);
  padding: 0;
  background: center/calc(0.4rem + 1.07vmin) calc(0.6rem + 1.6vmin) no-repeat white;
  border: none;
  transition: background-color 0.2s, box-shadow 0.2s;
}
@media (orientation: landscape) {
  .controls > button {
    width: 5.6rem;
    height: 5.6rem;
    background-size: auto;
  }
}
.controls > button:focus {
  outline: none;
  box-shadow: inset 0 0 0 0.1rem #babacf;
}
.controls > button:hover, .controls > button:active {
  background-color: #e6e6e6;
}
.controls > button:nth-child(1) {
  background-image: url(icon-prev.svg);
  border-top-left-radius: calc(1rem + 2.7vmin);
  border-bottom-left-radius: calc(1rem + 2.7vmin);
}
@media (orientation: landscape) {
  .controls > button:nth-child(1) {
    border-top-left-radius: 2.8rem;
    border-bottom-left-radius: 2.8rem;
  }
}
.controls > button:nth-child(2) {
  background-image: url(icon-next.svg);
  border-top-right-radius: calc(1rem + 2.7vmin);
  border-bottom-right-radius: calc(1rem + 2.7vmin);
}
@media (orientation: landscape) {
  .controls > button:nth-child(2) {
    border-top-right-radius: 2.8rem;
    border-bottom-right-radius: 2.8rem;
  }
}

.quote {
  margin-top: 5.4vh;
  padding: 3.6vh 2.66vw;
  color: #202046;
  text-align: center;
  background: top center/calc(3rem + 8vw) calc(2.5rem + 3.75vh) no-repeat url(pattern-quotes.svg);
}
@media (orientation: landscape) {
  .quote {
    flex: 0 0 44.375vw;
    margin-top: 2vh;
    margin-right: -9.375vw;
    padding: 8vh 0 0;
    text-align: left;
    background-position: 6.66vw top;
    background-size: calc(6rem + 4.17vw) calc(5rem + 3.47vw);
  }
}
.quote > p {
  font: 300 4.8vmin/135% Inter, sans-serif;
  font-size: calc( 1.8rem + 1.4 * ((100vw - 37.5rem) / 106.5) );
}
@media (orientation: landscape) {
  .quote > p {
    line-height: 140%;
    font-kerning: none;
  }
}
.quote > p::before {
  content: "“ ";
}
.quote > p::after {
  content: " ”";
}
.quote > footer {
  display: flex;
  flex-flow: column;
  margin-top: 3vh;
}
@media (orientation: landscape) {
  .quote > footer {
    flex-flow: row;
    margin-top: 5vh;
  }
  .quote > footer > cite + cite {
    margin-left: 0.8rem;
  }
}
.quote > footer > cite {
  font-size: calc( 1.5rem + 0.5 * ((100vw - 37.5rem) / 106.5) );
  font-family: Inter, sans-serif;
  font-kerning: none;
  line-height: normal;
}
.quote > footer > cite:nth-child(1) {
  font-weight: 700;
}
.quote > footer > cite:nth-child(2) {
  color: #babacf;
  font-weight: 500;
}

@keyframes hide {
  to {
    transform: translateX(-3rem) scale(0.95);
    opacity: 0;
  }
}
@keyframes show {
  from {
    transform: translateX(3rem) scale(1.05);
    opacity: 0;
  }
}
@keyframes hide-reverse {
  to {
    transform: translateX(3rem) scale(1.05);
    opacity: 0;
  }
}
@keyframes show-reverse {
  from {
    transform: translateX(-3rem) scale(0.95);
    opacity: 0;
  }
}
.hide {
  animation: hide 0.25s;
}
.hide-reverse {
  animation: hide-reverse 0.25s;
}

.show {
  animation: show 0.25s;
}
.show-reverse {
  animation: show-reverse 0.25s;
}